//Packages
import React, { useEffect } from 'react';
import { FiDollarSign, FiArrowRight } from 'react-icons/fi';

//Style
import '../styles/converter.css';

//Moedas
import moedas from '../moedas.json';

export default function Converter() {

    function handleCurrency() {
        const select1 = document.querySelector("select[name=valor1]");
        const select2 = document.querySelector("select[name=valor2]");

        fetch(`https://free.currconv.com/api/v7/convert?q=${select1.value}_${select2.value}&compact=ultra&apiKey=ca91775d93fb27b33fd4`)
            .then(res => {
                res.json().then(data => {
                    const result = Object.values(data);
                    const actualValue = result[0] * document.querySelector("input[name=amount]").value;
                    console.log(actualValue);

                    const output = document.querySelector("#output");
                    output.innerHTML = `O valor de ${document.querySelector("input[name=amount]").value} ${select1.value} equivale à ${actualValue} ${select2.value}.`;
                });
            }).catch(err => {
                console.log(err);
                const output = document.querySelector("#output");
                output.innerHTML = `Erro na requisição. Tente novamente mais tarde.`;
            });
    }

    useEffect(() => {
        const select1 = document.querySelector("select[name=valor1]");
        const select2 = document.querySelector("select[name=valor2]");

        const alreadyIn = [];

        for (const moeda of moedas) {
            if (moeda.AlphabeticCode != null && alreadyIn.indexOf(moeda.AlphabeticCode) === -1) {
                alreadyIn.push(moeda.AlphabeticCode);
                select1.innerHTML += `<option value="${moeda.AlphabeticCode}">${moeda.AlphabeticCode}</option>`;
                select2.innerHTML += `<option value="${moeda.AlphabeticCode}">${moeda.AlphabeticCode}</option>`;
            }
        }

        select1.value = "BRL";
        select2.value = "USD";
    });

    return (
        <div id="page-converter">
            <div className="converter-background-opacity">
                <div className="converter-frame">
                    <div className="title"><FiDollarSign></FiDollarSign><h1>converta.me</h1></div>
                    <div className="converter-content">
                        <div className="amount">
                            <h1>Quantidade</h1>
                            <input defaultValue="1" type="number" name="amount"></input>
                        </div>
                        <div className="from-to">
                            <h1>De</h1>
                            <select name="valor1"></select>
                            <h1>para</h1>
                            <select name="valor2"></select>
                        </div>
                    </div>
                    <a onClick={() => { handleCurrency() }}>Converter moedas <FiArrowRight></FiArrowRight></a>
                    <div className="output-frame">
                        <h1 id="output">Aguardando requisição..</h1>
                    </div>
                </div>
            </div>
        </div>
    );
}