//Packages
import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { FiX, FiDollarSign, FiArrowRight } from 'react-icons/fi';

//Style
import '../styles/main.css';

function handleCloseModal() {
    const modal = document.querySelector(".modal");
    modal.classList.add("hidden");
}

function handleOpenModal() {
    const modal = document.querySelector(".modal");
    if(modal != null){
        modal.classList.remove("hidden");
    }
}

export default function Main() {

    const history = useHistory();

    useEffect(() => {
        setTimeout(() => {
            handleOpenModal();
        }, 2500);
    });

    return (
        <div id="page-main">
            <div className="main-background-opacity">
                <div className="main-frame">
                    <div className="main-content">
                        <div className="title"><FiDollarSign></FiDollarSign><h1>converta.me</h1></div>
                        <h2>Faça conversões de moedas de maneira rápida</h2>
                        <a onClick={()=>{ history.push("/converter"); }}>Comece a converter <FiArrowRight></FiArrowRight></a>
                    </div>
                </div>

                <div className="modal hidden">
                    <FiX id="modal-close" onClick={() => { handleCloseModal() }}></FiX>
                    <div className="modal-frame">
                        <div className="modal-content">
                            <h1>Olá usuário!</h1>
                            <h2>Gostou da ideia? Eu tenho um GitHub onde posto mais conteúdo por lá.</h2>
                            <a href="https://github.com/theMatth33w/" target="_blank">Acessar meu GitHub</a>
                        </div>
                    </div>
                </div>

                <div className="main-bottom-text">
                    <p>App desenvolvido por Matheus Ibrahim. O código fonte dessa aplicação está disponível no meu <a href="https://github.com/theMatth33w/" target="_blank">GitHub</a>.</p>
                </div>
            </div>
        </div>
    );
}